import React from 'react'
import {Element} from 'react-scroll/modules'
import Project from '../Project/Project'
import './ProjectContainer.css'

const ProjectContainer = () => {

    const projects = [{
            img: 'https://media.istockphoto.com/photos/abstract-wavy-object-picture-id1198271727?b=1&k=20&m=1198271727&s=170667a&w=0&h=b626WM5c-lq9g_yGyD0vgufb4LQRX9UgYNWPaNUVses=',
            title: 'prj1',
            desc: 'description',
            link: 'www.google.com'
        },
        {
            img: 'https://media.istockphoto.com/photos/abstract-wavy-object-picture-id1198271727?b=1&k=20&m=1198271727&s=170667a&w=0&h=b626WM5c-lq9g_yGyD0vgufb4LQRX9UgYNWPaNUVses=',
            title: 'prj1',
            desc: 'description',
            link: 'www.google.com'
        },
        {
            img: 'https://media.istockphoto.com/photos/abstract-wavy-object-picture-id1198271727?b=1&k=20&m=1198271727&s=170667a&w=0&h=b626WM5c-lq9g_yGyD0vgufb4LQRX9UgYNWPaNUVses=',
            title: 'prj1',
            desc: 'description',
            link: 'www.google.com'
        },
        {
            img: 'https://media.istockphoto.com/photos/abstract-wavy-object-picture-id1198271727?b=1&k=20&m=1198271727&s=170667a&w=0&h=b626WM5c-lq9g_yGyD0vgufb4LQRX9UgYNWPaNUVses=',
            title: 'prj1',
            desc: 'description',
            link: 'www.google.com'
        },
    ]

    return (
        <Element id='projects' className='projectContainer'>
            <h1>Projects</h1>
            <p>Here are projects which I have done.</p>
            <div className="projectContainer__projects">
                {
                projects.map((project, index) => {
                    return (
                        <Project key={index}
                            img={
                                project.img
                            }
                            title={
                                project.title
                            }
                            desc={
                                project.desc
                            }
                            link={
                                project.link
                            } />
                    );
                })
            } </div>
        </Element>
    )
}

export default ProjectContainer
