import React, { useState } from 'react'
import {Link} from 'react-scroll'
import './Header.css'
import MenuIcon from '@mui/icons-material/Menu';
import CloseIcon from '@mui/icons-material/Close';

function Header() {

    const [open, setOpen] = useState(false)
    const closeMenu = () =>{
        setOpen(false)
    }
    
    const handleClick = () =>{
        setOpen(!open)
    }
  return (
    <div className='header'>
        <div className="header__left">
            <h1>Develop<span>er</span></h1>
        </div>

        <div className="header__mobicons" onClick={handleClick}>
            {open ? <CloseIcon /> : <MenuIcon />}
        </div>

        <div className={open ? "header__right active" : "header__right"}>
            <Link to='about' smooth={true} duration={500} onClick={closeMenu}>
                <h4>About Me</h4>
            </Link>
            <Link to='skills' smooth={true} duration={500} onClick={closeMenu}>
                <h4>Skills</h4>
            </Link>
            <Link to='projects' smooth={true} duration={500} onClick={closeMenu}>
                <h4>Projects</h4>
            </Link> 
            {/* <Link to='experience' smooth={true} duration={500}>
                <h4>Experience</h4>
            </Link> */}
            <Link to='contact' smooth={true} duration={500} onClick={closeMenu}>
                <h4>Contact</h4>
            </Link>
            <h4 className='header__rightButton'>
                <a href="https://bit.ly/3zm3O7h">Join with me</a>
            </h4>
        </div>
    </div>
  )
}

export default Header